div.acco {
  position: relative; }

input[type=checkbox] {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0; }

h2.accor {
  font-size: 18px;
  font-weight: 700;
  color: #0c2340;
  margin: 40px 0 0 0; }

label {
  cursor: pointer; }

label {
  position: relative;
  display: block;
  padding-left: 15px;
  line-height: 1.3; }

label:before {
  content: "";
  position: absolute;
  width: 0;
  height: 0;
  top: 50%;
  left: 0px;
  border-left: 5px solid black;
  border-top: 5px solid transparent;
  border-bottom: 5px solid transparent;
  margin-top: 10px; }

input[type=checkbox]:checked ~ h2.accor label:before {
  border-left: 5px solid transparent;
  border-top: 5px solid black;
  border-right: 5px solid transparent;
  margin-left: -3px;
  margin-top: 13px; }

p.acc {
  max-height: 0;
  overflow: hidden;
  padding-left: 0px;
  transition: max-height 0.4s ease;
  font-size: 80%;
  margin-bottom: 5px;
  color: #777; }

input[type=checkbox]:checked ~ h2 ~ p.acc {
  max-height: 120px; }

